(() => {
	var MetroLines = new Map();
	window.EventMetro = {
		sub (line, callback) {
			if (!(callback instanceof Function)) return;
			var cbs = MetroLines.get(line);
			if (!cbs) {
				cbs = new Set();
				MetroLines.set(line, cbs);
			}
			cbs.add(callback);
		},
		unsub (line, callback) {
			var cbs = MetroLines.get(line);
			if (!cbs) return;
			cbs.delete(callback);
		},
		pub (line, ...args) {
			var cbs = MetroLines.get(line);
			if (!cbs) return;
			for (let cb of cbs) {
				cb(...args);
			}
		},
		clear (line) {
			var cbs = MetroLines.get(line);
			if (!cbs) return;
			cbs.clear();
		},
		clearAll () {
			MetroLines.clear();
		},
		get channels () {
			return [...MetroLines.keys()];
		},
		countForChannel (line) {
			var metros = MetroLines.get(line);
			return metros.size;
		}
	};
}) ();