<template>
	<div class="reg_container" :show="show">
		<div class="frame login" :show="!reg">
			<div class="title">登录</div>
			<div class="line"></div>
			<div class="item">
				<span>用户名：</span>
				<input ref="username" type="text" placeholder="请输入用户名" @keydown.esc="fadeout" @keydown.enter="login">
			</div>
			<div class="item">
				<span>密　码：</span>
				<input ref="password" type="password" placeholder="请输入密码" @keydown.esc="fadeout" @keydown.enter="login">
			</div>
			<div class="line"></div>
			<div class="control">
				<button @click="login">登录</button>
				<button @click="fadeout">取消</button>
				<button @click="callReg">注册</button>
			</div>
		</div>
		<div class="frame reg" :show="reg">
			<div class="title">注册</div>
			<div class="line"></div>
			<div class="item">
				<span>用 户 名：</span>
				<input ref="usernamer" type="text" placeholder="请输入用户名">
			</div>
			<div class="item">
				<span>密　　码：</span>
				<input ref="passwordr" type="password" placeholder="请输入密码">
			</div>
			<div class="item">
				<span>确认密码：</span>
				<input ref="repassword" type="password" placeholder="请确认密码">
			</div>
			<div class="line"></div>
			<div class="item">
				<span>所在公司：</span>
				<input ref="company" type="text" placeholder="请输入公司名">
			</div>
			<div class="line"></div>
			<div class="control">
				<button @click="signup">注册</button>
				<button @click="quitsignup">取消</button>
			</div>
		</div>
	</div>
</template>

<style scoped>
.reg_container {
	position: fixed;
	top: 0%;
	left: 0%;
	z-index: 1;
	width: 100%;
	height: 0%;
	background-color: rgb(36, 36, 36, 0.3);
	opacity: 0;
	transition: opacity 300ms ease-in-out, height 300ms step-end;
	overflow: hidden;
	pointer-events: none;
}
.reg_container[show="true"] {
	opacity: 1;
	height: 100%;
	transition: opacity 300ms ease-in-out, height 300ms step-start;
	pointer-events: auto;
}
.frame {
	position: absolute;
	top: 0%;
	left: 50%;
	padding: 15px;
	background-color: rgb(245, 245, 245);
	border-radius: 10px;
	border: 1px solid rgb(175, 175, 175);
	transform: translate(-50%, -100%);
	transition: all 300ms ease-in-out;
	opacity: 0;
	pointer-events: none;
}
.frame[show="true"] {
	top: 50%;
	transform: translate(-50%, -50%);
	opacity: 1;
	pointer-events: auto;
}
.frame > .title {
	margin-bottom: 10px;
	text-align: center;
	font-size: 28px;
	font-weight: bolder;
}
.frame > .line {
	height: 1px;
	margin-top: 10px;
	margin-bottom: 10px;
	background-color: rgb(175, 175, 175);
	overflow: hidden;
}
.frame > .item {
	padding: 5px 10px;
}
.frame > .control {
	margin-top: 15px;
	text-align: center;
}
.frame input {
	padding: 2px 5px;
	background-color: transparent;
	outline: none;
	border: none;
	border-bottom: 1px solid rgb(36, 36, 36);
}
.frame button {
	padding: 10px 20px;
	margin: 0px 15px;
	background-color: white;
	outline: none;
	border-radius: 5px;
	border: none;
	box-shadow: inset 0px 0px 2px rgb(205, 205, 205), 1px 1px 3px rgba(75, 75, 75, 0.2);
	font-size: 14px;
	cursor: pointer;
	transition: all 300ms ease-in-out;
}
.frame button:hover {
	box-shadow: inset 0px 0px 0px rgb(175, 175, 175), 2px 2px 5px rgba(36, 36, 36, 0.5);
}
</style>

<script>
var current;
EventMetro.sub('show_login', () => {
	if (!current) return;
	current.popup();
});

export default {
	name: "Register",
	data () {
		return {
			show: false,
			reg: false
		}
	},
	mounted () {
		current = this;
	},
	unmounted () {
		current = null;
	},
	methods: {
		popup () {
			this.reg = false;
			this.show = true;
			this.$refs.username.focus();
		},
		async login () {
			var username = this.$refs.username.value;
			if (!username) {
				notify({
					type: 'warn',
					title: '用户名不能为空',
					duration: 2000
				});
				this.$refs.username.focus();
				return;
			}
			var password = this.$refs.password.value;
			if (!password) {
				notify({
					type: 'warn',
					title: '密码不能为空',
					duration: 2000
				});
				this.$refs.password.focus();
				return;
			}

			var result = await BackEnd.signin(username, password);
			if (!result) {
				return;
			}
			notify({
				type: 'success',
				title: '登录成功',
			});
			localStorage.set('sessionID', result.session);
			this.show = false;
			EventMetro.pub('signin', true, result.name, result.company);
		},
		async signup () {
			var username = this.$refs.usernamer.value;
			if (!username) {
				notify({
					type: 'warn',
					title: '用户名不能为空',
					duration: 2000
				});
				this.$refs.usernamer.focus();
				return;
			}
			var password = this.$refs.passwordr.value;
			if (!password) {
				notify({
					type: 'warn',
					title: '密码不能为空',
					duration: 2000
				});
				this.$refs.passwordr.focus();
				return;
			}
			var repwd = this.$refs.repassword.value;
			if (!repwd) {
				notify({
					type: 'warn',
					title: '确认密码不能为空',
					duration: 2000
				});
				this.$refs.repassword.focus();
				return;
			}
			if (repwd !== password) {
				notify({
					type: 'warn',
					title: '两次输入密码不一致',
					duration: 2000
				});
				this.$refs.repassword.focus();
				return;
			}
			var company = this.$refs.company.value;
			if (!company) {
				notify({
					type: 'warn',
					title: '公司名不能为空',
					duration: 2000
				});
				this.$refs.company.focus();
				return;
			}

			var result = await BackEnd.signup(username, password, company);
			if (!result) {
				return;
			}
			notify({
				type: 'success',
				title: '注册成功'
			});
			this.quitsignup();
		},
		fadeout () {
			this.show = false;
			EventMetro.pub('signin', false);
		},
		quitsignup () {
			this.reg = false;
			EventMetro.pub('signup');
		},
		callReg () {
			this.reg = true;
		},
	}
}
</script>