var WSReady = false;
const WSPenddingRequests = [];
const WSRequestMap = new Map();

const newID = () => Math.floor(Math.random() * 10000000);
const loadedJobs = [];

(() => {
	var domain = location.origin, port = domain.match(/:\d+$/);
	if (!!port) {
		domain = domain.replace(/:\d+$/, ":3000");
	}
	else {
		domain = domain + ':3000';
	}
	var script = document.createElement('script');
	script.src = domain + '/socket.io/socket.io.js';
	script.onload = () => {
		domain = domain.replace(/^https?/, 'ws');
		window.socketIO = io(domain);
		socketIO.on("connect", () => {
			console.log('WS-Back connected!');
			WSReady = true;

			loadedJobs.forEach(task => task());
			loadedJobs.splice(0, loadedJobs.length);

			var tasks = WSPenddingRequests.splice(0, WSPenddingRequests.length);
			tasks.forEach(async task => {
				if (!task.res) return;
				var handler = BackEndServer[task.event];
				var result;
				if (!!handler) {
					let param = task.args || [];
					result = await handler(...param);
					task.res(result);
				}
			});
		});
	};
	document.head.appendChild(script);

	const BackEndServer = {};
	const newRequestTask = (event) => {
		const ErrorHint = !!event.match(/^send/i) ? '提交数据失败' : '载入数据失败';
		BackEndServer[event] = (...args) => new Promise(res => {
			if (!WSReady) {
				WSPenddingRequests.push({ event, args, res });
				return;
			}
			var tid = newID();
			WSRequestMap.set(tid, res);
			socketIO.emit(event, tid, ...args);
		});
		var task = () => {
			socketIO.on(event, (tid, result) => {
				var res = WSRequestMap.get(tid);
				if (!res) return;
				WSRequestMap.delete(tid);
				if (!result || !result.ok) {
					let msg = {
						type: 'error',
						title: ErrorHint
					};
					if (!!result && !!result.err) msg.message = result.err;
					notify(msg);

					if (!!result && !!result.needLogin) {
						localStorage.removeItem('sessionID');
					}

					result = null;
				}
				else if (!!result.data) {
					result = result.data;
				}
				res(result);
			});
		};
		if (WSReady) task();
		else loadedJobs.push(task);
	};

	window.BackEnd = new Proxy(BackEndServer, {
		get: (target, prop, receiver) => {
			if (Reflect.has(target, prop)) return target[prop];
			newRequestTask(prop);
			return target[prop];
		}
	});
}) ();