import './assets/js/eventMetro.js';
import { createApp } from 'vue'
import App from './App.vue'
import router from './router.js';
import Notification from './components/notification.vue';
import './assets/js/socket.js';

window.wait = (delay = 0) => new Promise(res => {
	setTimeout(res, delay);
});
window.stamp2string = stamp => {
	var time = new Date(stamp);
	var Y = time.getYear() + 1900;
	var M = time.getMonth() + 1;
	if (M < 10) M = '0' + M;
	var D = time.getDate();
	if (D < 10) D = '0' + D;
	var h = time.getHours();
	if (h < 10) h = '0' + h;
	var m = time.getMinutes();
	if (m < 10) m = '0' + m;
	return Y + '/' + M + '/' + D + ' ' + h + ':' + m;
};
localStorage.__proto__.set = function (key, value) {
	this.setItem(key, JSON.stringify(value));
};
localStorage.__proto__.get = function (key) {
	var value = this.getItem(key);
	var obj;
	try {
		obj = JSON.parse(value);
	}
	catch {
		obj = value;
	}
	return obj;
};

const app = createApp(App);
app.use(router);
app.use(Notification);
app.mount('#app');